<template>
	<div class="container" :class="styles">
		<div class="row d-flex justify-content-center" v-if="notStarted">
			<button class="gamebtn margintop" @click="clickStarted" v-if="hasProgress">
				Продолжить игру  
			</button>
			<button class="gamebtn margintop" @click="restartGame">
				Начать новую игру 
			</button>
		</div>
		<div class="row d-flex justify-content-center" v-else>
			<div class="container">
				<div class="row">
					<div class="col">
						<button class="gamebtn" @click="exit">
							Выход в меню 
						</button>
					</div>
					<div class="col">
						<button class="gamebtn" @click="restartLevel">
							Начать уровень заново
						</button>
					</div>
				</div>
			</div>
			<p style="margin-top: 10px">Уровень {{ level }}</p>
			<div class="container">
				<div class="row">
					<div class="col">
						<p>Инвестиционная сумма: </p>
						<input type="number" name="sum" @keyup.enter="onEnter"
								placeholder="введите сумму"
								style="width: 12rem; height:  3rem;"
								v-model="invest">
					</div>
					<div class="col">
						<p>Число сделок в уровне: {{ total }}</p>
						<p>Совершено сделок: {{ cnt }}</p>
					</div>
				</div>
			</div>
			<div class="d-flex justify-content-center">
				<p style="margin-top: 10px">
					Текущий капитал: {{ money }} 
					<br> 
					<span style="opacity: 0.5;"> (рекорд: {{ record }}) </span> 
				</p>
			</div>
			<div class="container">
				<div class="row">
					<div class="col-lg-3 col-md-1"></div>
					<div class="col-lg-6 col-md-10 col-sm-12 col-xs-12">
						<div class="graph">
							<Scatter :data="chartData" :options="chartOptions" :style="myStyles"/>
						</div>
					</div>
					<div class="col-lg-3 col-md-1"></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { Scatter } from 'vue-chartjs'
import {
  Chart as ChartJS,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  Legend
} from 'chart.js'
import * as chConfig from './chartConfig.js'
ChartJS.register(LinearScale, PointElement, LineElement, Tooltip, Legend)
export default {
	name: "GameView",
	components: { Scatter },
	computed: {
		chartData: { 
			get () { 
				return {
					ticks: [ { fontColor: '#FFFFFF' } ],
					datasets: [ {
						fill: false,
						borderColor: '#6600CC',
						borderWidth: 5,
						backgroundColor: '#6600CC',
						data: this.points
					} ] 
				}
			},
			set (value) {
				this.points = value
			}
		},
		chartOptions() { return chConfig.options },
		myStyles () { return chConfig.styles }
	},
	data () {
		return {
			notStarted: true,
			hasProgress: false,
			styles: 'bkgOut',
			level: 1,
			total: 30,
			cnt: 0,
			record: 0,
			money: 100,
			invest: null,
			points: [
				{
					x: 0,
					y: 1
				}
			],
			percent: 66
		}
	},
	methods: {
		clickStarted () {
			this.notStarted = false
			this.styles = 'bkgIn'
		},
		exit () {
			this.notStarted = true
			this.styles = 'bkgOut'
		},
		endGame () {
			let record = this.record
			localStorage.clear()
			this.record = record
			this.hasProgress = false
			this.cnt = 0
			this.level = 1
			this.money = 100
			this.percent = 66
			this.invest = null
			let pointY = Math.floor(this.money / 100)
			if (this.money % 100 >= 0.5) 
				pointY = Math.ceil(this.money / 100)
			this.chartData = [{x: 0, y: pointY}]
		},
		restartGame () {
			this.endGame()
			this.clickStarted() 
		},
		restartLevel () {
			this.cnt = 0
			this.invest = null
			this.money = 100
			let pointY = Math.floor(this.money / 100)
			if (this.money % 100 >= 0.5) 
				pointY = Math.ceil(this.money / 100)
			this.chartData = [{x: 0, y: pointY}]
		},
		onEnter () {
			this.cnt = this.cnt + 1
			if (!this.invest || this.invest > this.money || this.invest <= 0) {
				alert('Инвестируемый капитал должен быть от $1 до величины счета')
				this.invest = null
				this.cnt = this.cnt - 1
			}
			else if (this.money >= 100000) {
				alert('Поздравляем! Вы достигли максимального капитала. Ваш капитал: ' + this.money)
				this.endGame()
				this.exit()
			}
			else if (this.cnt == 30) {
				if (this.money > 100) {
					alert('Поздравляем, Вы успешно перешли на следующий уровень')
					this.cnt = 0
					this.level = this.level + 1
					this.money = 100
					this.invest = null
					let pointY = Math.floor(this.money / 100)
					if (this.money % 100 >= 0.5) 
						pointY = Math.ceil(this.money / 100)
					this.chartData = [{x: 0, y: pointY}]
					this.percent = this.percent - 4
				}
				else {
					alert('Вы продержались на этом уровне, но уменьшили капитал')
					this.endGame()
					this.exit()
				}
				if (this.level > 10 || this.percent <= 10) {
					alert('Поздравляем! Вы прошли все уровни. Ваш капитал: ' + this.money)
					this.endGame()
					this.exit()
				}
			}
			else {
				if (100 * Math.random() < this.percent)
					this.money = this.money + this.invest
				else
					this.money = this.money - this.invest
				let pointY = Math.floor(this.money / 100)
				if (this.money % 100 >= 0.5) 
					pointY = Math.ceil(this.money / 100)
				
				let point = {
						x: this.cnt,
						y: pointY
					}
				let points = this.points.concat(point)
				this.invest = null
				this.chartData = points
				if (this.money <= 0) {
					alert('Наверное, Вам стоит еще поиграть')
					this.endGame()
					this.exit()
				}
			}
		}
	},
	mounted() {
		if (localStorage.money) {
			this.money = eval(localStorage.money)
		}
		if (localStorage.level) {
			this.level = eval(localStorage.level)
		}
		if (localStorage.record) {
			this.record = eval(localStorage.record)
		}
		if (localStorage.cnt) {
			this.cnt = eval(localStorage.cnt)
		}
		if (localStorage.points) {
			this.points = JSON.parse(localStorage.points)
		}
		if (localStorage.invest) {
			this.invest = eval(localStorage.invest)
		}
		if (localStorage.hasProgress) {
			this.hasProgress = localStorage.hasProgress
		}
	},
	watch: {
		money(newMoney) {
			localStorage.money = eval(newMoney);
			if (newMoney > this.record) {
				this.record = eval(newMoney)
			}
			if (!this.hasProgress && newMoney != 100) {
				this.hasProgress = true
			}
		},
		level(newLevel) {
			localStorage.level = eval(newLevel)
		},
		record(newRecord) {
			localStorage.record = eval(newRecord)
		},
		cnt(newCnt) {
			localStorage.cnt = eval(newCnt)
		},
		points(newPoints) {
			localStorage.points = JSON.stringify(newPoints)
		},
		invest(newInvest) {
			localStorage.invest = eval(newInvest)
		},
		hasProgress(newValue) {
			localStorage.hasProgress = eval(newValue)
		}
	}
}
</script>

